.social_icons {
    height:3%;
    padding: 2% 5%;
    display: flex;
    justify-content: space-evenly;
    background-color: #282c34;
    flex-wrap: wrap;
    position: sticky;
    z-index:2;
    bottom:0;
    right: 0;
  
  }
  
  .social_icons a {
    color:rgb(245, 183, 183);
    text-decoration: none;
  }

  @media only screen and (max-width: 600px) {
    .social_icons {
      max-width: 70%;
      margin: auto;
      padding:5% 0;
      align-items: flex-start;
   flex-direction: column;
     
    
    }
  
  }